/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 20px;
  height: 18px;
  top: 10px;
  left: 4px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #000;
  box-shadow: 0 0 2px rgba(200, 200, 200, 0.75);
  border-radius: 6px;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  padding: 1em 0;
}

a.bm-item, a.bm-item:link, a.bm-item:hover {
  text-decoration: none;
  color: #fff;
}

a.bm-item:hover {
  color: #efefef;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
body {
  color: #fff;
  font-family: Montserrat, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h1 {
  font-size: 4em;
}

h2 {
  font-size: 3em;
}

h1, h2, h3, h4, h5 {
  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

#root {
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background-image: url(/static/media/background.7e1b6b7f.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

#page, .transition-group {
  flex-basis: 100%;
  flex-grow: 1;
  text-align: center;
  display: flex;
  flex-direction: row;
}

@media (max-width: 640px) {
  #page {
    flex-wrap: wrap;
    padding-top: 85px;
  }
  #page.admin {
    padding-top: 0;
  }
}

.transition-group {
  position: relative;
}

.router {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}

.fade-enter {
  opacity: 0;
}

.slide-enter.slide-enter-active {
  opacity: 1;
  transition: opacity 150ms ease-in;
}

.slide-exit {
  opacity: 0;
}

#page {
  color: #ffffff;
}

#page.admin {
  color: #111111;
}

